import React, { useEffect, useState } from 'react';
import { Header } from './components/Header';
import { Main } from './components/Main';
import { Footer } from './components/Footer';
import { data } from './api';

function App() {
  const [dataFromServer, setData] = useState([]);
  const [visibleData, setVisibleData] = useState([]);

  console.log('visible data:', visibleData);

  useEffect(() => {
    setData(data);
  }, []);

  useEffect(() => {
    setVisibleData(dataFromServer);
  }, [dataFromServer]);

  const updateData = (newData) => {
    setData([...dataFromServer, newData]);
  };

  const updateTitle = (postId, newTitle) => {
    const findPost = visibleData.find(post => post.id === postId);

    findPost.title = newTitle;
  };

  const deletePost = (postId) => {
    setData(dataFromServer.filter(post => post.id !== postId));
  };

  const changeCheckox = (postId, newChecked) => {
    const updatedPost = visibleData.find(post => post.id === postId);

    updatedPost.completed = newChecked;
  };

  const filterData = (filterBy) => {
    if (filterBy === undefined) {
      setVisibleData(dataFromServer);
    } else {
      setVisibleData(dataFromServer
        .filter(post => post.completed === filterBy));
    }
  };

  const clearAllCompleted = () => {
    setData(dataFromServer.filter(post => post.completed !== true));
  };

  return (
    <section className="todoapp">
      <Header
        onSubmit={updateData}
        newId={dataFromServer.length + 1}
      />

      <Main
        data={visibleData}
        changeCheckob={changeCheckox}
        onDelete={deletePost}
        onUpdateTitle={updateTitle}
      />

      <Footer
        filterData={filterData}
        itemsLength={visibleData.length}
        clearAllCompleted={clearAllCompleted}
      />
    </section>
  );
}

export default App;
